/*eslint-disable*/
import React from "react";
import {useState}from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
// @material-ui/icons
import Mail from "@material-ui/icons/Mail";
import Favorite from "@material-ui/icons/Favorite";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Footer from "components/Footer/Footer.js";

import creditDark from "assets/img/credit-card-dark.png";
import creditWhite from "assets/img/credit-card-white.png";
import debitDark from "assets/img/debit-bank-dark.png";


import styles from "assets/jss/nextjs-material-kit-pro/pages/justfit/justfit.js";
import Justfit from "../../components/Justfit/justfit";
import PlanHorizontal from "../../components/Justfit/JustfitPlans/PlanHorizontal";
import { Grid } from "@material-ui/core";

const useStyles = makeStyles(styles);

const [firstCardColor,setFirstCardColor]=useState('#484848');
const [firstCardTextColor,setFirstCardTextColor]=useState('white');
// const [firstCardIMG,setfirstCardIMG]=useState('FIT');
// const [color,setColor]=useState('#D3D3D3');
// const [textColor,setTextColor]=useState('#CCDA01');
// const [textPlano,setTextPlano]=useState('FIT');



export default function SectionPayment() {
  const classes = useStyles();
  return (
      
      <GridContainer className={classes.content} justify="center" >        
        <GridItem xs={12} sm={12} md={12}>
          <Justfit
            theme="white"
            className={classes.jusfit}
          >
           <GridContainer justify='left' style={{marginBottom:'150px',}}>
               <GridItem xs={12} sm={12} md={10} style={{marginBottom:'40px'}}>
             <h1 style={{fontSize:'40px',fontWeight:600,color:"#484848",maxWidth:'500px'}}>ESCOLHA SEU MEIO DE PAGAMENTO</h1>
             </GridItem>
             <GridItem xs={12} sm={12} md={10}>
             <h1 style={{fontSize:'14px',fontWeight:600,color:"#484848",maxWidth:'500px',paddingBottom:'20px'}}>Selecione uma das opções de pagamento e preencha os campos abaixo</h1>
             </GridItem>
             <GridItem xs={12} sm={12} md={3} >
     <button className={classes.btnCardChecked} style={{backgroundColor:firstCardColor,color:firstCardTextColor}}>
      <span></span>
      <img src={creditDark} style={{mixBlendMode:'difference'}}/>
      <h2>Cartão de Crédito</h2>
     </button>
     </GridItem>
     <GridItem xs={12} sm={12} md={3} align='center' justify='center'>
     <button className={classes.btnCard}>
     <span></span>
     <img src={creditDark} />
      <h2>Cartão de Débito</h2>
     </button>
  </GridItem>
  <GridItem xs={12} sm={12} md={3}>
  <button className={classes.btnCard}>
      <span></span>
      <img src={debitDark} />
      <h2>Débito em conta</h2>
  </button>
  </GridItem>
                <GridItem xs={12} sm={12} md={6} style={{marginTop:'50px'}}>
                <GridContainer justify='left' >
                <GridItem xs={12} sm={12} md={10}> 
                 <h5 style={{color:"#484848",fontSize:'18px',fontWeight:600,paddingTop:'50px !important',padding:0,margin:0,}}>Informações de Pagamento</h5>
             </GridItem>
                 <GridItem xs={12} sm={12} md={10} className={classes.formInputItem}>
                 <h2 >Nome impresso no Cartão</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={5} className={classes.formInputItem}>
                 <h2>Número do Cartão</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={5} className={classes.formInputItem}>
                 <h2>CPF do Titular do Cartão</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={3} className={classes.formInputItem}>
                 <h2>Vencimento</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={4} className={classes.formInputItem}>
                 <h2>Cod. Segurança</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={3} className={classes.formInputItem}>
                 <h2>Nº de parcelas</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={10} className={classes.checkboxItemPayment}>
                 <div style={{display: 'flex', flexDirection: 'row', alignItems:'center',}}>
                      <input type='checkbox' style={{marginRight:'10px'}} />
                      <h2>Li e aceito o contrato, o <a>termo de adesão</a> e o <a>regulamento interno</a>.</h2>
                      </div>
                 </GridItem>
                 </GridContainer>
                 </GridItem>

                <GridItem xs={12} sm={12} md={6} style={{marginTop:'50px'}}>
                <GridContainer justify='left' >
                <GridItem xs={12} sm={12} md={10}> 
                 <h5 style={{color:"#484848",fontSize:'18px',fontWeight:600,paddingTop:'50px !important',padding:0,margin:0,}}>Endereço de cobrança</h5>
             </GridItem>

                 <GridItem xs={12} sm={12} md={5} className={classes.formInputItem}>
                 <h2 >CEP</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={5} className={classes.formInputItem}>
                 <h2>Logradouro</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={3} className={classes.formInputItem}>
                 <h2>Número</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={5} className={classes.formInputItem}>
                 <h2>Complemento</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={2} className={classes.formInputItem}>
                 <h2>UF</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={5} className={classes.formInputItem}>
                 <h2>Bairro</h2>
                 <input></input>
                 </GridItem>
                 <GridItem xs={12} sm={12} md={5} className={classes.formInputItem}>
                 <h2>Cidade</h2>
                 <input></input>
                 </GridItem>
                 </GridContainer>
                 </GridItem>
            </GridContainer>
 
          
          </Justfit>
        
        </GridItem>
      </GridContainer>
      
       
      
  );
}
